.input_header {
  color: #aaaaad;
  padding-top: 10px;
  padding-bottom: 10px;
  .div1 {
    background-color: #23232e;
    padding: 0.5vw;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
}

.filter {
  display: flex;
  flex-direction: column;
  padding-top: 2vh;
  padding-left: 3vw;
  padding-right: 3vw;
}
.search_filter {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 5vh;
  button {
    padding-left: 7px;
    padding-right: 7px;
    height: 30px;
    margin-top: 14px;
  }
}

.table {
}
thead {
  font-weight: 700;
}
.table_full {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding-top: 8vh;
  padding-bottom: 3vh;
  padding-left: 2vw;
  padding-right: 2vw;
  margin-bottom: 10vh;
  margin-top: 3vh;
}
.pagination {
  display: inline-block;
}

.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
}
.pagination a.active {
  background-color: #4caf50;
  color: white;
}

.absentees_details {
  display: flex;
  flex-direction: row;
  min-height: 14vh !important;
  justify-content: space-around;
}
.block {
  min-width: 20% !important;
  padding-top: 1vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: white;
  font-weight: 750;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.flex-container {
  display: flex;
  flex-direction: row;
}
.icons {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 10%;
}
.view_absentees {
  padding-top: 6vh;
}

.modalp {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 9999999999999999999999999999999999; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
.modal-contentp {
  position: fixed;
  background-color: #ffffff;
  margin: 15% 35%; /* 15% from the top and centered */
  padding: 10px;
  border: 1px solid #888;
  width: 30%;
  height: auto;
  .modal-headerp {
    padding: 2px 16px;
    background-color: #337ab7;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  h2 {
    color: white;
    font-size: 17px;
    padding-left: 20px;
    padding-top: 10px;
  }
  .closep {
    color: white;
  }
  .profilep {
    background-color: rgb(238, 238, 238);
    width: auto;
    height: 78%;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10vh;
    border-style: ridge;
    border-color: rgba(128, 128, 128, 0.075);
    margin: 10px;
    margin-top: 15px;
  }
  // .submit-part input {
  //   width: 100%;
  //   padding-top: 5px;
  //   padding-bottom: 5px;
  //   margin-bottom: 6px;
  //   border-style: ridge;
  //   box-sizing: border-box !important;
  //   border-color: rgba(0, 0, 0, 0.397) !important;
  // }
  .submit-part button {
    float: right;
    margin-top: 4vh;
    padding-top: 4px;
    padding-bottom: 4px;
    margin-left: 5px;
  }
}

/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 800px) {
  .absentees_details {
    flex-direction: column;
  }

  .modal-contentp {
    width: 100%;
    margin: 0px;
    margin-top: 10vh;
  }
}
