@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap");

.demo {
  /* Main Content */
  .preloader {
    position: relative;
    height: 100%;
    width: 100%;
    z-index: 2 !important;
    background-color: #ffffff;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(./images/preloader/preloader.gif);
  }
  .preloader2 {
    position: relative;
    margin-top: 7vh;
    min-height: 30vh !important;
    width: 100%;
    z-index: 2 !important;
    background-color: #ffffff;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(./images/preloader/preloader.gif);
  }
  .mbox {
    margin-left: 166px;
    width: 100%;
    height: auto;
    color: black;
    overflow-y: scroll !important ;
    overflow-x: hidden;
    margin-bottom: 3vh;
  }
  .col1 {
    z-index: 1 !important;
  }
  .col1 {
    margin-left: 66px;
    text-align: left !important;
    width: 100%;
    height: 6vh;
    z-index: 9 !important;
    background-color: white;
    margin-top: 1vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .col1 h1 {
    padding-top: 1vh;
    padding-left: 2vw;
    color: #24242c;
    font-family: "Lato", sans-serif !important;
    font-size: 1.6rem;
    font-weight: bold;
  }

  @media only screen and (max-width: 825px) {
    .last {
      margin-bottom: 12px;
    }
    .mbox {
      margin-left: 0px;
    }
    .col1 {
      margin-left: 0;
      position: relative;
    }
  }

  // Attendence page content
  .main-grid {
    display: grid;
    left: 30px !important;
    grid-template-columns: 77vw;
    grid-template-rows: 3vh auto auto;
    grid-row-gap: 1vh;
    margin-top: 11vh;
    padding-left: 7%;
  }
  .sec {
    padding: 1vh 25px;
    background-color: #ffffff;
    font-weight: 600;
    border-right: 1px solid #b6b6b6;
    font-size: 0.9rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .dim {
    font-weight: bolder;
    border-bottom: 1px solid black !important;
  }
  .grid1 {
    background: #ffffff;
    padding-top: 1vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .he {
    width: 100%;
    height: 6vh;
    border-bottom: 1px solid #b6b6b6;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto;
  }
  .head {
    padding-top: 2.5vh;
    margin-left: 2vw;
  }

  // Filter box content
  .filter_box {
    margin: 2vh 5%;
    width: 90%;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 1vh 0;
  }
  .table-fixed tbody {
    height: 300px;
    overflow-y: auto;
    width: 100%;
    font-size: small;
    overflow-x: auto;
  }

  .table-fixed thead,
  .table-fixed tbody,
  .table-fixed tr,
  .table-fixed td,
  .table-fixed th {
    display: block;
  }

  .table-fixed tbody td,
  .table-fixed tbody th,
  .table-fixed thead > tr > th {
    float: left;
    position: relative;

    &::after {
      content: "";
      clear: both;
      display: block;
    }
  }
  .form_font {
    font-size: 0.9rem !important;
  }
  .ic {
    width: 0.9rem;
    color: #337ab7 !important;
  }
  .form_width {
    width: 25% !important;
  }
  .form_width2 {
    width: 50% !important;
  }
  .form_width3 {
    width: 40% !important;
  }
  // Room Management Page

  .first_container {
    margin: 2vh 7.5%;
    width: 85%;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .first_container_sub {
    margin-left: 5%;
    width: 90%;
    height: auto;
    .progress-bar-striped {
      color: white !important;
    }
  }
  .category_column {
    width: 40% !important;
    height: auto;
    padding-bottom: 3vh;
    margin: 15px !important;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .keyboard_style:hover {
    background-color: black !important;
  }
  .sp_style {
    padding: 3vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  /* large screens */
  @media only screen and (min-width: 825px) {
    main {
      padding-top: 12vh;
    }
    .col1 {
      position: fixed;
    }
    /*table section*/
    .grid3 {
      grid-area: 1/2/3/2;
