    }

    .table {
      width: 100% !important;
    }
    .table tr {
      height: 10vh !important;
      width: 100vw !important;
      display: inline-block !important;
    }
    .table tbody tr {
      display: inline-block !important;
    }
    .table,
    .table-fixed,
    .table tbody {
