/* // Modal box code
  // Part1: */

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 9999999999999999999999999999999999; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.modal-header {
  padding: 2px 16px !important;
  background-image: linear-gradient(to right, #0670cb, #4e8fc6);
  color: white;
}
.modal-header h2 {
  font-size: 1rem;
  text-align: center;
}

/* Modal Content/Box */
.modal-content {
  position: fixed;
  background-color: #ffffff;
  margin: 15% 35%; /* 15% from the top and centered */
  padding: 10px;
  border: 1px solid #888;
  width: 30% !important;
  height: auto; /* Could be more or less, depending on screen size */
}
.profile {
  /*background-color: rgb(211, 207, 207);*/
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: auto;
  height: 78%;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-style: ridge;
  border-color: rgba(128, 128, 128, 0.075);
  margin: 10px;
  margin-top: 15px;
}
.txt {
  margin: 0 15px;
}
.submit-part input {
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 6px;
  border-style: ridge;
  box-sizing: border-box !important;
  border-color: rgba(0, 0, 0, 0.397) !important;
}
.submit-part button {
  float: right;
  margin-top: 4vh;
  padding-top: 4px;
  padding-bottom: 4px;
  margin-left: 5px;
}
.close-button {
  background-color: #337ab7;
  color: snow;
  border: 0.5px solid blue;
}
/* The Close Button */
.close {
  color: rgb(255, 255, 255);
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

@media only screen and (min-width: 825px) {
